import React from 'react'
import styles from '../../styles/letter.module.css'

export default class Letters extends React.Component {

	constructor(props) {
		super(props)
		this.state = {}
	}

	render() {
		return(
			<div className="column letters">
				<p className={styles.title}>Inbox</p>
			</div>
		);
	}


} 